import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B9"
                meta="Toda la información que necesitas sobre la vitamina B9. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B9?"
                description="Toda la información que necesitas sobre la vitamina B9. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B9?"
                lang="es"
            />

            <h1>Vitamina B9</h1>
            <PostIndex2 linksData={[
                {"url": "#Funciones", "text": "Funciones"},
                {"url": "#alimentos", "text": "Alimentos"},
                {"url": "#Deficiencia", "text": "Deficiencia"},
                {"url": "#Exceso", "text": "Exceso"},
            ]}
            />
            <hr/>
            <p class="introductionText">La vitamina B9, tambi&eacute;n llamada &aacute;cido f&oacute;lico y folato, es soluble en el agua, y pertenece al grupo de las <a className="link" href="/vitaminaB">vitaminas B</a>. No
                puede ser almacenada en la grasa. La vitamina B9 debe ser suministrada por los alimentos porque el
                cuerpo no puede fabricarla. Su forma sint&eacute;tica, el &aacute;cido f&oacute;lico, se utiliza como
                suplemento, mientras que el folato se encuentra de forma natural en los alimentos vegetales.&nbsp;</p>
            <p></p>
            <hr/>
            <p>La vitamina B9 entra en la s&iacute;ntesis de ADN y ARN, que son necesarios para la formaci&oacute;n de
                prote&iacute;nas. Por lo tanto, es especialmente importante durante los per&iacute;odos de crecimiento
                r&aacute;pido como el desarrollo fetal, la infancia y la adolescencia, debido a su papel en la
                producci&oacute;n de nuevas c&eacute;lulas.&nbsp;</p>
            <p></p>
            <h2 name={"Funciones"}>&iquest;Qu&eacute; hace la vitamina B9?&nbsp;</h2>
            <p></p>
            <p>La vitamina B9 juega un papel importante en:&nbsp;</p>
            <p></p>
            <ul>
                    <li>La formación de los glóbulos rojos</li>
                    <li>El funcionamiento del sistema nervioso e inmunológico</li>
                    <li>La curación de las heridas</li>
                    <li>La reducción de los riesgos de malformaciones del tubo neural en los fetos</li>
                    <li>La reducción del riesgo de enfermedades cardiovasculares.</li>
            </ul>

            <h2 name={"alimentos"}>Fuentes de alimentos con vitamina B9</h2>
            <p>La vitamina B9 se encuentra en varios alimentos, incluyendo legumbres, verduras de color verde oscuro,
                h&iacute;gado, leche y queso, entre otros. Dado que la vitamina B9 es sensible al aire, la luz y el
                calor, su contenido en algunos alimentos puede verse afectado por el enlatado, la congelaci&oacute;n o
                la cocci&oacute;n.&nbsp;</p>
            <p></p>
            <p>Fuentes alimenticias de vitamina B9 (folatos):&nbsp;</p>
            <p></p>
                <div class="dataTable">
                <tbody>
                <tr>
                        <td><b>Alimentos</b></td>
                        <td><b>Tama&ntilde;o de la porci&oacute;n</b></td>
                        <td><b>Folatos*</b></td>
                </tr>
                <tr>
                        <td>Despojos de ave, asados o cocidos</td>
                        <td>100g (3.5oz)</td>
                        <td>345-770&micro;g</td>
                </tr>
                <tr>
                        <td>H&iacute;gado de cordero y ternera, salteado</td>
                        <td>100g (3.5oz)</td>
                        <td>331-400&micro;g</td>
                </tr>
                <tr>
                        <td>Legumbres, cocinadas</td>
                        <td>100g (3.5oz)</td>
                        <td>229-368&micro;g</td>
                </tr>
                <tr>
                        <td>H&iacute;gado de cerdo, cocido o salteado</td>
                        <td>100g (3.5oz)</td>
                        <td>100g (3.5oz)</td>
                </tr>
                <tr>
                        <td>Espinacas, hervidas</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>139&micro;g</td>
                </tr>
                <tr>
                        <td>Esp&aacute;rragos, hervidos</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>134&micro;g</td>
                </tr>
                <tr>
                        <td>Pasta enriquecida, cocinada</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>120-125&micro;g</td>
                </tr>
                <tr>
                        <td>Linaza</td>
                        <td>60 ml (0,25 taza)</td>
                        <td>108&micro;g</td>
                </tr>
                <tr>
                        <td>Soja, hervida o salteada</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>83-106&micro;g</td>
                </tr>
                <tr>
                        <td>Br&oacute;coli, hervido</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>89&micro;g</td>
                </tr>
                <tr>
                        <td>Lechuga romana</td>
                        <td>250 ml (1 taza)</td>
                        <td>64&micro;g</td>
                </tr>
                <tr>
                        <td>Semillas de girasol, tostadas</td>
                        <td>60 ml (0,25 taza)</td>
                        <td>78&micro;g</td>
                </tr>
                <tr>
                        <td>Mantequilla de semillas de girasol</td>
                        <td>30 ml (2 cucharadas)</td>
                        <td>78&micro;g</td>
                </tr>
                <tr>
                        <td>Remolachas, cocinadas</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>72&micro;g</td>
                </tr>
                <tr>
                        <td>Soja germinada</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>64&micro;g</td>
                </tr>
                <tr>
                        <td>Espinacas crudas</td>
                        <td>250 ml (1 taza)</td>
                        <td>61&micro;g</td>
                </tr>
                <tr>
                        <td>Jugo de naranja</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>58&micro;g</td>
                </tr>
                <tr>
                        <td>Coles de Bruselas, cocidas</td>
                        <td>4 brotes (80 g)</td>
                        <td>50&micro;g</td>
                </tr>
                <tr>
                        <td>Okra (gumbo), hervida</td>
                        <td>125 ml (0,5 taza)</td>
                        <td>39&micro;g</td>
                </tr>
                <tr>
                        <td>Nueces, avellanas y avellanas, deshidratadas, no blanqueadas</td>
                        <td>60 ml (0,25 taza)</td>
                        <td>33&micro;g</td>
                </tr>
                </tbody>
                </div>

            <p></p>
            <p></p>
            <h2 name={"Deficiencia"}>Deficiencia vitamina B9</h2>
            <p>La deficiencia de vitamina B9 es com&uacute;n en nuestra sociedad, que tiende a consumir productos
                alimenticios manufacturados m&aacute;s ricos en grasas y refinados a expensas de una ingesta adecuada de
                frutas y verduras.&nbsp;</p>
            <p></p>
            <p>Las deficiencias ocurren en algunas personas en las siguientes situaciones:&nbsp;</p>
            <p></p>
                <ul>
                        <li>Embarazo y lactancia</li>
                        <li>Alcoholismo</li>
                        <li>Diarrea sostenida</li>
                        <li>Fiebre persistente</li>
                        <li>Anemia</li>
                        <li>Enfermedades intestinales</li>
                        <li>Enfermedades del hígado</li>
                        <li>Trastorno de la función renal</li>
                        <li>Cirugía de estómago.</li>
                </ul>


            <p></p>
            <p>Si ya tiene un ni&ntilde;o con una anomal&iacute;a del tubo neural o hay antecedentes de ella en su
                familia, hable con su m&eacute;dico. Algunas mujeres necesitan hasta 5 mg al d&iacute;a.&nbsp;</p>
            <p></p>
            <p>Los diab&eacute;ticos, los epil&eacute;pticos y los obesos corren un mayor riesgo de tener un hijo con
                una anomal&iacute;a del tubo neural y deben consultar a su m&eacute;dico antes de planificar un
                embarazo.&nbsp;</p>
            <p></p>
            <h3>Signos de deficiencia de vitamina B9 (&aacute;cido f&oacute;lico):&nbsp;</h3>
            <p></p>
            <ul>
                    <li>Diarrea;</li>
                    <li>Apetito y p&eacute;rdida de peso;</li>
                    <li>Inflamaci&oacute;n de la lengua (glositis);</li>
                    <li>Dolores de cabeza;</li>
                    <li>Palpitaciones card&iacute;acas;</li>
                    <li>Irritabilidad y cambios de humor.</li>
            </ul>
            <p>Una deficiencia grave puede causar el agotamiento de los gl&oacute;bulos rojos en la sangre, la
                alteraci&oacute;n de la mucosa del cuello uterino, los intestinos, el est&oacute;mago y la vagina (las
                c&eacute;lulas se vuelven malignas) y retrasos en el crecimiento.&nbsp;</p>
            <p></p>
            <p></p>
            <p></p>
            <h2 name={"Exceso"}>Signos de exceso de vitamina B9 (m&aacute;s de 5 mg al d&iacute;a):&nbsp;</h2>
            <p></p>
            <p>Inicio o empeoramiento de complicaciones neurol&oacute;gicas (hormigueo o entumecimiento de las
                extremidades, dificultad para caminar, p&eacute;rdida de memoria, demencia, trastornos del estado
                de &aacute;nimo) en personas que sufren de deficiencia de vitamina B12;</p>
            <ul>
                    <li>Problemas digestivos;</li>
                    <li>Irritabilidad;</li>
                    <li>Confusi&oacute;n;</li>
                    <li>Alteraci&oacute;n de los ciclos de sue&ntilde;o;</li>
                    <li>Reacci&oacute;n al&eacute;rgica (poco frecuente).</li>
            </ul>
        </Layout>
    )
}

export default Mac
